.lucky-number-form {
  border: 1px solid #acbec5;
  border-radius: 1rem;
  padding: 10px;
  max-width: 500px;
  margin: 25px auto;
  text-align: center;
  transition: all 0.3s ease;
}

.lucky-number-form h2 {
  font-size: 18px;
  margin: 20px auto;
  color: #111827;
}

.lucky-number-form input {
  font-family: 'Space Grotesk';
  width: 100%;
  padding: 0.6rem 1rem;
  font-size: 1rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  margin-bottom: 1rem;
  transition: border-color 0.2s;
  max-width: 50px;
}

.lucky-number-form input:focus {
  outline: none;
  border-color: #acbec5;
  box-shadow: 0 0 0 3px rgba(99, 168, 241, 0.2);
}

.lucky-number-form button {
  font-family: 'Space Grotesk';
  background-color: #000f67;
  color: white;
  font-weight: 500;
  padding: 0.6rem 1.2rem;
  font-size: 1rem;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: background-color 0.2s;
  margin-left: 10px;
}

.lucky-number-form button:hover {
  background-color: #41868f;
}

.lucky-number-form button:disabled {
  background-color: #c7d2fe;
  cursor: not-allowed;
}

.error {
  color: #dc2626;
  margin-top: 0.5rem;
  font-size: 0.9rem;
}
